import React, { useState, useEffect } from "react";

import Modal from "react-modal";
import FormProfissional from "../Form/FormProfissional";

export default function ModalIsntESF(props) {
  console.log(
    "ESTOU EM ModalIsntESF.js | VIM DE APP.js, após clicar em ações de uma unidde sem ser ESF"
  );

   //UTILIZA A TECLA ESC PARA FECHAR A MODAL
 useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });
  
  const handleKeyDown = (event) => {
    if (event.key === "Escape") {
      onClose(null);
    }
  };
  const handleModalClose = (event) => {
    onClose(null);
  };

  const { profesionaisIsntESF, onClose } = props;
  return (
    <div>
      <Modal isOpen={true}>
      <div style={styles.flexRow}>

      <span style={styles.title}>Detalhe do Profissional</span>

      <button onClick={handleModalClose} className="waves-effect waves-lights btn red dark-4">
            X
      </button>
      </div>
      <FormProfissional profissional={profesionaisIsntESF}/>

      </Modal>

    </div>
  );
}

const styles = {
  flexRow: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    marginBottom: "40px",
  },

  title: {
    textAlign: "center",
    alignItems: "center",
    fontSize: "1.3rem",
    fontWeight: "bold",
  },

  errorMessage: {
    color: "red",
    fontWeight: "bold",
  },
};
