import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import FormProfissional from "../Form/FormProfissional";

Modal.setAppElement("#root");

export default function ModalDetailProfissionalArea(props) {
  console.log(
    "ESTOU EM ModalDetailProfissionalArea.js | VIM DE APP.js, POREM ACIONADO APÓS CLICAR EM DETALHAR PROFISSIONAL DE AREA"
  );
  const {
    profissionalId,
    allProfessionalWithoutName,
    allProfessionalWithName,
    onClose,
  } = props;

  const selectedProfissionalWithoutName = allProfessionalWithoutName.find(
    (professional) => professional.PROF_ID === profissionalId
  );
  const selectedProfessionalWithName = allProfessionalWithName.find(
    (professional) => professional.PROF_ID === profissionalId
  );

  const { MICROAREA } = selectedProfissionalWithoutName;
  //CBO: selectedProfessionalWithName.VINCULOS_PROF.DADOS_VINC_PROF.COD_CBO
  //CONSELHO: selectedProfessionalWithName.VINCULOS_PROF.DADOS_VINC_PROF.CONSELHOID

  const handleModalClose = (event) => {
    onClose(null);
  };

  //UTILIZA A TECLA ESC PARA FECHAR A MODAL
  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });

  const handleKeyDown = (event) => {
    if (event.key === "Escape") {
      onClose(null);
    }
  };

  return (
    <div>
      <Modal isOpen={true}>
        <div style={styles.flexRow}>
          <span style={styles.title}>Detalhe do Profissional</span>
          <button
            className="waves-effect waves-lights btn red dark-4"
            onClick={handleModalClose}
          >
            X
          </button>
        </div>

        <FormProfissional profissional={selectedProfessionalWithName} />
      </Modal>
    </div>
  );
}

const styles = {
  flexRow: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    marginBottom: "40px",
  },

  title: {
    textAlign: "center",
    alignItems: "center",
    fontSize: "1.3rem",
    fontWeight: "bold",
  },

  errorMessage: {
    color: "red",
    fontWeight: "bold",
  },
};
