import React, { useState, useEffect } from "react";
import Spinner from "./components/Spinner/Spinner";
import UBSControl from "./components/UBS/UBSControl";
import ModalDetailUBS from "./components/Modal/ModalDetailUBS";
import ModalDetailArea from "./components/Modal/ModalDetailArea";
import ModalDetailProfissionalArea from "./components/Modal/ModalDetailProfissionalArea";
import ModalIsntESF from "./components/Modal/ModalIsntESF";


export default function App() {
  /*allUBS receberá todo o arquivo do XML/JSON COM OS DADOS DO CNES DO CLIENTE*/
  const [allUBS, setAllUBS] = useState([]);
  const [selectedUBS, setSelectedUBS] = useState({});
  const [isModalDetailUBSOpen, setIsModalDetailUBSOpen] = useState(false);

  /*IMPORTARÁ E RECEBERÁ DE UMA API COM OS CBOs DO BRASIL*/
  const [allCBOs, setAllCBOs] = useState({});

  const [selectedArea, setSelectedArea] = useState({});
  const [isModalDetailAreaOpen, setIsModalDetailAreaOpen] = useState(false);

  /* MODAL QUE DETALHA PROFISSIONAL */
  const [
    isModalDetailProfissionalAreaOpen,
    setIsModalDetailProfissionalAreaOpen,
  ] = useState(false);

  const [profissionalId, setProfissionalId] = useState({});
  const [allProfessionalWithoutName, setAllProfessionalWithoutName] = useState(
    {}
  );
  const [allProfessionalWithName, setAllProfessionalWithName] = useState({});

  /*MODAL DE PROFISSIONAIS NAO ESF*/
  const [isModalIsntESFOpen, setIsModalIsntESFOpen] = useState(false);
  const [profesionaisIsntESF, setProfefissionalIsntESF] = useState({});

  /*FUNÇÃO QUE IMPORTA O ARQUIVO DO CNES XML, JÁ CONVERTIDO EM JSON*/
  function cnesImport() {
    let json = require("./API/CNES.json");
    const allEstabecimentos = json.ESTABELECIMENTOS.map((estabelecimento) => {
      return {
        UNIDADE_ID: estabelecimento.UNIDADE_ID,
        CNES: estabelecimento.CNES,
        EQUIPES: estabelecimento.EQUIPES,
        PROFISSIONAIS: estabelecimento.PROFISSIONAIS,
        SERVICOS_ESPECIALIZADOS: estabelecimento.SERVICOS_ESPECIALIZADOS,
        NOME_FANTA: estabelecimento.NOME_FANTA,
      };
    });
    return allEstabecimentos;
  }

  useEffect(() => {
    const UBS = cnesImport();
    setTimeout(() => {
      setAllUBS(UBS);
    }, 2000);
  }, []);

  function cboImport() {
    let json = require("./API/CBO.json");
    const CBOs = json.map((cbo) => {
      return {
        cbo: cbo.code,
        descricao: cbo.name,
        descricaoFormatada: cbo.name.toUpperCase(),
        descricaoCompletaFormatada: cbo.code + " - " + cbo.name.toUpperCase(),
      };
    });
    return CBOs;
  }

  useEffect(() => {
    const CBO = cboImport();
    setAllCBOs(CBO);
  }, []);

  const handlePersistUBS = (UBS) => {
    setSelectedUBS(UBS);
    setIsModalDetailUBSOpen(true);
  };

  const handleOnPersistSelectedEquipe = (selectedEquipe) => {
    setSelectedArea(selectedEquipe);
    setIsModalDetailAreaOpen(true);
  };

  const handlePersistSelectedProfissional = (
    profissionalId,
    allProfessionalWithoutName,
    allProfessionalWithName
  ) => {
    setIsModalDetailProfissionalAreaOpen(true);
    setIsModalDetailAreaOpen(false);
    setProfissionalId(profissionalId);
    setAllProfessionalWithoutName(allProfessionalWithoutName);
    setAllProfessionalWithName(allProfessionalWithName);
  };

  const handlePersistWithoutArea = (profissional) => {
    setIsModalIsntESFOpen(true);
    setProfefissionalIsntESF(profissional);
  }

  const handleCloseDetailArea = () => {
    setIsModalDetailAreaOpen(false);
  };

  const handleCloseModalIsntESF = () => {
    setIsModalIsntESFOpen(false);

  }

  const handleCloseDetailProfissionalArea = () => {
    setIsModalDetailProfissionalAreaOpen(false);

  }

  const handleCloseDetailUBS = () => {
    setIsModalDetailUBSOpen(false);
  }


  return (
    <div className="container">
      <h1 className="center">CNES - PARANAGUÁ</h1>
      {allUBS.length === 0 && <Spinner />}
      {/*SE O TAMANHO DE allUBS FOR IGUAL A 0, ELE DEMONSTRARÁ A TELA DE LOAD*/}
      {allUBS.length >= 34 && (
        <UBSControl allUBS={allUBS} onPersist={handlePersistUBS} />
      )}
      {/*SE O TAMANHO DE allUBS FOR NO MINIMO 34, ELE DEMONSTRARÁ TODO O CONTEÚDO DE UBS */}
      {isModalDetailUBSOpen && (
        <ModalDetailUBS
          selectedUBS={selectedUBS}
          onPersist={handleOnPersistSelectedEquipe}
          onPersistWithoutArea={handlePersistWithoutArea}
          onClose={handleCloseDetailUBS}
          allCBOs={allCBOs}
        />
      )}
      {/*SE CLICAR NO ACTION, ABRIRÁ A MODAL. */}

      {isModalDetailAreaOpen && (
        <div>
          <ModalDetailArea
            selectedArea={selectedArea}
            allUBS={allUBS}
            onClose={handleCloseDetailArea}
            allCBOs={allCBOs}
            onPersist={handlePersistSelectedProfissional}
          />
        </div>
      )}

      {isModalDetailProfissionalAreaOpen && (
        <ModalDetailProfissionalArea
          profissionalId={profissionalId}
          allProfessionalWithoutName={allProfessionalWithoutName}
          allProfessionalWithName={allProfessionalWithName}
          onClose={handleCloseDetailProfissionalArea}
        />
      )}

      {isModalIsntESFOpen && (
        <ModalIsntESF profesionaisIsntESF={profesionaisIsntESF} onClose={handleCloseModalIsntESF}>

        </ModalIsntESF>
      )}
    </div>
  );
}
