import React, { useState, useEffect } from "react";
import EquipesUBSESF from "./EquipesUBSESF";
import OthersUBS from "./OthersUBS";

export default function SelectedUBS(props) {
  console.log("ESTOU EM SelectedUBS.js | VIM DE ModalDetailUBS.js");

  const { selectedUBS, onPersist, allCBOs, onPersistWithoutArea } = props;

  const EquipeLength = selectedUBS.EQUIPES.length;

  const selectedEquipes = selectedUBS.EQUIPES.map((equipe) => {
    return {
      COD_AREA: equipe.COD_AREA,
      CO_EQUIPE: equipe.CO_EQUIPE,
      DS_EQUIPE: equipe.DS_EQUIPE,
      DS_SEGMENTO: equipe.DS_SEGMENTO,
      UNIDADE_ID: equipe.UNIDADE_ID,
      PROF_EQUIPE: equipe.PROF_EQUIPE,
      NM_REFERENCIA: equipe.NM_REFERENCIA,
    };
  });

  const handleOnPersistSelectedEquipe = (selectedEquipe) => {
    onPersist(selectedEquipe);
  };

  const handleOnPersistSProfissionalWithoutArea = (id) => {
    onPersistWithoutArea(id);
  };

  return (
    <div>
      <div style={styles.title}>
        <span>{selectedUBS.CNES} - {selectedUBS.NOME_FANTA} </span>

      </div>
      {EquipeLength > 0 && (
        <EquipesUBSESF
          equipes={selectedEquipes}
          onPersist={handleOnPersistSelectedEquipe}
        />
      )}
      {EquipeLength === 0 && (
        <OthersUBS
          selectedUBS={selectedUBS}
          allCBOs={allCBOs}
          onPersist={handleOnPersistSProfissionalWithoutArea}
        />
      )}
    </div>
  );
}

const styles = {
  title: {
    textAlign: "center",
    alignItems: "center",
    fontSize: "1.3rem",
    fontWeight: "bold",
    marginBottom: "40px",

  },
};
