import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import SelectedUBS from "../UBS/SelectedUBS";


Modal.setAppElement("#root");

export default function ModalDetailUBS(props) {
  console.log(
    "ESTOU EM ModalDetailUBS.js | VIM DE APP.js, após setar em TRUE para abrir esta tela"
  );

  const { selectedUBS, onPersist, onClose, allCBOs, onPersistWithoutArea} = props;
 // console.log(selectedUBS);

 const handleOnPersistSelectedEquipe = (selectedEquipe) => {
    onPersist(selectedEquipe);
 }

 const handleOnPersistWithoutArea = (id) => {
   onPersistWithoutArea(id);
}

 //UTILIZA A TECLA ESC PARA FECHAR A MODAL
 useEffect(() => {
  document.addEventListener("keydown", handleKeyDown);
  return () => {
    document.removeEventListener("keydown", handleKeyDown);
  };
});

const handleKeyDown = (event) => {
  if (event.key === "Escape") {
    onClose(null);
  }
};
const handleModalClose = (event) => {
  onClose(null);
};




  return (
    <div>
      <Modal isOpen={true}>
        <div style={styles.flexRow}>
          <span style={styles.title}>Informações da Unidade</span>
          <button onClick={handleModalClose} className="waves-effect waves-lights btn red dark-4">
            X
          </button>
        </div>
        <SelectedUBS selectedUBS={selectedUBS} onPersist={handleOnPersistSelectedEquipe} onPersistWithoutArea={handleOnPersistWithoutArea} allCBOs={allCBOs}/>

      </Modal>
    </div>
  );
}

const styles = {
  flexRow: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    marginBottom: "40px",
  },

  title: {
    textAlign: "center",
    alignItems: "center",
    fontSize: "1.3rem",
    fontWeight: "bold",
  },

  errorMessage: {
    color: "red",
    fontWeight: "bold",
  },
};
