import React from "react";
import Action from "../Action/Action";

export default function ProfissionaisArea(props) {
  console.log("ESTOU EM ProfissionaisArea.js | VIM DE SelectedArea.js");
  const { profissionaisArea, selectedUBS, allCBOs, onPersist } = props;

  const listProfissionaisArea = profissionaisArea.map((profissional) => {
    const nomeProfissional = selectedUBS.PROFISSIONAIS.find(
      (nome) => nome.PROF_ID === profissional.PROF_ID
    );
    const actualCBO = nomeProfissional.VINCULOS_PROF.DADOS_VINC_PROF.COD_CBO;
    const detailCBO = allCBOs.find((cbo) => cbo.cbo === actualCBO);

    const handlePersistProfissionalArea = (profissionalId, allProfessionalWithoutName, allProfessionalWithName) => {
        onPersist(profissionalId, allProfessionalWithoutName, allProfessionalWithName);
    };

    return (
      <tr key={nomeProfissional.PROF_ID}>
        <td style={{ width: "40%" }}>{nomeProfissional.NOME_PROF}</td>
        <td style={{ width: "60%" }}>{detailCBO.descricaoCompletaFormatada}</td>
        <td style={{ width: "10%" }}>
          <Action
            type={"info_outline"}
            id={nomeProfissional.PROF_ID}
            onActionClick={handlePersistProfissionalArea}
            allProfessionalWithoutName={profissionaisArea}
            allProfessionalWithName={selectedUBS.PROFISSIONAIS}
          />
        </td>
      </tr>
    );
  });
  return <tbody>{listProfissionaisArea}</tbody>;
}
