import React from "react";
import ProfissionaisArea from "./ProfissionaisArea";

export default function SelectedArea(props) {
  console.log("ESTOU EM SelectedArea.js | VIM DE ModalDetailArea.js");
  const { selectedArea, selectedUBS, allCBOs, onPersist } = props;

  const handlePersist = (
    profissionalId,
    allProfessionalWithoutName,
    allProfessionalWithName
  ) => {
    onPersist(
      profissionalId,
      allProfessionalWithoutName,
      allProfessionalWithName
    );
  };

  return (
    <div>
      <div style={styles.title}>
        <span>
          {selectedUBS.NOME_FANTA}
          <p>{selectedArea.COD_AREA} - {selectedArea.NM_REFERENCIA}</p>

        </span>
      </div>

      <table className="striped">
        <thead>
          <tr>
            <th style={{ width: "40%" }}> {`NOME`} </th>
            <th style={{ width: "60%" }}>{`CBO`}</th>
            <th style={{ width: "10%" }}>AÇÕES</th>
          </tr>
        </thead>

        <ProfissionaisArea
          profissionaisArea={selectedArea.PROF_EQUIPE}
          selectedUBS={selectedUBS}
          allCBOs={allCBOs}
          onPersist={handlePersist}
        />
      </table>
    </div>
  );
}

const styles = {
  title: {
    textAlign: "center",
    alignItems: "center",
    fontSize: "1.3rem",
    fontWeight: "bold",
    marginBottom: "40px",
  },
};
