import React from "react";
import Action from "../Action/Action";

export default function UBSControl(props) {
  /*VEIO DE APP PARA UBS CONTROL*/
  console.log("ESTOU EM UBSCONTROL.js | VIM DE APP.js");

  const { allUBS, onPersist } = props;

  const handleActionClick = (id, type) => {
    const UBS = allUBS.find((findUBS) => findUBS.UNIDADE_ID === id);
    onPersist(UBS);
}

  return (
    <div>
      <table className="striped">
        <thead>
          <tr>
            <th style={{ width: "10%" }}>CNES</th>
            <th style={{ width: "70%" }}>NOME UNIDADE</th>
            <th style={{ width: "20%" }}>AÇÕES</th>
          </tr>
        </thead>
        <tbody>
          {allUBS.map((UBS) => {
            return (
              <tr key={UBS.CNES}>
                <td style={{ width: "10%" }}>{UBS.CNES}</td>
                <td style={{ width: "70%" }}>{UBS.NOME_FANTA}</td>
                <td><Action type="info_outline" onActionClick={handleActionClick} id={UBS.UNIDADE_ID}/></td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
