import React from "react";
import Action from "../Action/Action";

export default function OthersUBS(props) {
  console.log("ESTOU EM OthersUBS.js | VIM DE SelectedUBS.js");
  const { selectedUBS, allCBOs, onPersist} = props;

  
  const handlePersistProfissionalArea = (id) => {
      onPersist(id);
  }
  const listProfissionais = selectedUBS.PROFISSIONAIS.map((profissional) => {
    const actualCBO = profissional.VINCULOS_PROF.DADOS_VINC_PROF.COD_CBO;
    const detailCBO = allCBOs.find((cbo) => cbo.cbo === actualCBO);



    return (
      <tr key={profissional.PROF_ID}>
        <td style={{ width: "40%" }}>{profissional.NOME_PROF}</td>
        <td style={{ width: "60%" }}>{detailCBO.descricaoCompletaFormatada}</td>
        <td style={{ width: "10%" }}>
        <Action
            type={"info_outline"}
            onActionClick={handlePersistProfissionalArea}
            id={profissional}

        />
        </td>
      </tr>
    );
  });

  return (
    <table className="striped">
      <thead>
        <tr>
          <th style={{ width: "40%" }}> {`NOME`} </th>
          <th style={{ width: "60%" }}>{`CBO`}</th>
          <th style={{ width: "10%" }}>AÇÕES</th>
        </tr>
      </thead>
      <tbody>{listProfissionais}</tbody>
    </table>
  );
}
