import React from "react";
import Action from "../Action/Action";


export default function EquipesUBSESF(props) {
  console.log("ESTOU EM EquipesUBSESF.js | VIM DE SelectedUBS.js");
  const { equipes, onPersist } = props;

  const handleActionClick = (id, type) => {
    const equipe = equipes.find((equipe) => equipe.COD_AREA === id);
    onPersist(equipe);
}

  const listEquipes = equipes.map((equipe) => {
    return (
        <tr key={equipe.COD_AREA}>
            <td style={{ width: "05%" }}>{equipe.COD_AREA}</td>
            <td style={{ width: "10%" }}>{equipe.NM_REFERENCIA}</td>
            <td style={{ width: "10%" }}>{equipe.CO_EQUIPE}</td>
            <td style={{ width: "15%" }}>{equipe.DS_SEGMENTO}</td>
            <td style={{ width: "50%" }}>{equipe.DS_EQUIPE}</td>
            <td style={{ width: "5%" }}><Action type="info_outline" id={equipe.COD_AREA} onActionClick={handleActionClick}/></td>
        </tr>

    );
  });

  return (
      <table className="striped">
        <thead>
          <tr>
            <th style={{ width: "05%" }}>AREA</th>
            <th style={{ width: "10%" }}>NOME</th>
            <th style={{ width: "10%" }}>INE</th>
            <th style={{ width: "15%" }}>SEGMENTO</th>
            <th style={{ width: "50%" }}>DESCRIÇÃO DO TIPO DE EQUIPE</th>
            <th style={{ width: '5%' }}>AÇÕES</th>

          </tr>
        </thead>
        <tbody>
            {listEquipes}
        </tbody>

      </table>
  );
}
