import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import SelectedArea from "../UBS/SelectedArea";

Modal.setAppElement("#root");

export default function ModalDetailArea(props) {
  console.log(
    "ESTOU EM ModalDetailArea.js | VIM DE APP.js, POREM ACIONADO APÓS CLICAR EM DETALHAR AREA"
  );
  const { onClose, selectedArea, allUBS, allCBOs, onPersist } = props;

  const selectedUnidade = allUBS.find(
    (UBS) => UBS.UNIDADE_ID === selectedArea.UNIDADE_ID
  );

  //UTILIZA A TECLA ESC PARA FECHAR A MODAL
  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });

  const handleKeyDown = (event) => {
    if (event.key === "Escape") {
      onClose(null);
    }
  };

  const handleModalClose = () => {
    onClose(null);
  };

  const handlePersist = (profissionalId, allProfessionalWithoutName, allProfessionalWithName) => {
    onPersist(profissionalId, allProfessionalWithoutName, allProfessionalWithName);
  }

  return (
    <div className="container">
      <Modal isOpen={true}>
        <div style={styles.flexRow}>
          <span style={styles.title}>Informações das Areas</span>
          <button
            className="waves-effect waves-lights btn red dark-4"
            onClick={handleModalClose}
          >
            X
          </button>
        </div>
        <SelectedArea selectedArea={selectedArea} selectedUBS={selectedUnidade} allCBOs={allCBOs} onPersist={handlePersist}/>
      </Modal>
    </div>
  );
}

const styles = {
  flexRow: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    marginBottom: "40px",
  },

  title: {
    textAlign: "center",
    alignItems: "center",
    fontSize: "1.3rem",
    fontWeight: "bold",
  },

  errorMessage: {
    color: "red",
    fontWeight: "bold",
  },
};
