import React from 'react';

export default function Action({ id, type, onActionClick, allProfessionalWithoutName, allProfessionalWithName }) {

    const handleIconClick = () => {
        onActionClick(id, allProfessionalWithoutName, allProfessionalWithName);
    };

  return (
    <span
      className="material-icons"
      onClick={handleIconClick}
      style={{ cursor: 'pointer', width: '20%' }}
    >
      {type}
    </span>
  );
}
